import spriteRunLeft from '../img/spriteRunLeft.png'
import spriteRunRight from '../img/spriteRunRight.png'
import spriteStandLeft from '../img/spriteStandLeft.png'
import spriteStandRight from '../img/spriteStandRight.png'

const canvas = document.querySelector('canvas')
const c = canvas.getContext('2d')

const platform = crearImagen('./img/platform.jpg')
const hills = crearImagen('./img/hills.png')
const background = crearImagen('./img/background.png')
background.width = canvas.width
background.height = canvas.height

// Medidas del lienzo 
canvas.width = 1024
canvas.height = 668

const gravity = 0.8

// Imagenes
function crearImagen(src) {
  const image = new Image();
  image.src = src;
  return image;
}

// Crear al jugador -----------------------------
class Player {

  constructor() {
    this.position = {
      x: 100,
      y: 500
    }
    this.width = 100
    this.height = 150
    this.velocity = {
      x: 0,
      y: 1 // Gravedad
    }
    this.image = crearImagen(spriteStandRight)
    this.frames = 0
    this.sprites = {
      stand: {
        right: crearImagen(spriteStandRight),
        left: crearImagen(spriteStandLeft),
        cropWidth: 177,
        width: 66
      },
      run: {
        right: crearImagen(spriteRunRight),
        left: crearImagen(spriteRunLeft),
        cropWidth: 341,
        width: 126
      }
    }
    this.currentSprite = this.sprites.stand.right;
    this.currentCropWidth = 177
  }

  draw() {
    c.beginPath();
    c.drawImage(
      this.currentSprite,
      this.currentCropWidth * this.frames,
      0,
      this.currentCropWidth,
      400,
      this.position.x,
      this.position.y,
      this.width,
      this.height)
    c.closePath();
  }

  update() {
    this.draw();
    this.position.x += this.velocity.x
    this.position.y += this.velocity.y
    if (this.position.y + this.height + this.velocity.y <= canvas.height) {
      this.velocity.y += gravity
    } else {
      this.velocity.y = 0
    }
    this.frames++;
    if ((this.frames > 59 && this.currentSprite === this.sprites.stand.right) ||
      (this.frames > 59 && this.currentSprite === this.sprites.stand.left)) {
      this.frames = 0;
    } else if ((this.frames > 29 && this.currentSprite === this.sprites.run.right) ||
      (this.frames > 29 && this.currentSprite === this.sprites.run.left)) {
      this.frames = 0;
    }
  }

}

// Crear Plataformas -----------------------------
class Platform {

  constructor({ x, y, width, image }) {
    this.position = {
      x: x,
      y: y
    }
    this.image = image
    this.image.width = width
    this.width = this.image.width
    this.height = this.image.height
  }

  draw() {
    c.beginPath()
    //c.fillStyle = 'blue'
    //c.fillRect(this.position.x, this.position.y, this.width, this.height);
    c.drawImage(this.image, this.position.x, this.position.y)

    c.closePath()
  }
}


// Crear GenericObject -----------------------------
class GenericObject {

  constructor({ x, y, width, height, image }) {
    this.position = {
      x: x,
      y: y
    }
    this.image = image
    this.image.width = width
    this.image.height = height
    this.width = this.image.width
    this.height = this.image.height
  }

  draw() {
    c.beginPath()
    //c.fillStyle = 'blue'
    //c.fillRect(this.position.x, this.position.y, this.width, this.height);
    c.drawImage(this.image, this.position.x, this.position.y)

    c.closePath()
  }
}

//---------------------

let player = new Player();
const imagen = new Image()
imagen.src = platform.src
let platform1 = new Platform({ x: 20, y: canvas.height - 100, width: 200, image: imagen });
let platform2 = new Platform({ x: 320, y: canvas.height - 300, width: 200, image: imagen });
let platform3 = new Platform({ x: 320, y: canvas.height - 200, width: 200, image: imagen });
let platform4 = new Platform({ x: 920, y: canvas.height - 400, width: 200, image: imagen });
let platform5 = new Platform({ x: 1220, y: canvas.height - 500, width: 200, image: imagen });
let platform6 = new Platform({ x: 1500, y: canvas.height - 200, width: 200, image: imagen });
let platform7 = new Platform({ x: 1700, y: canvas.height - 400, width: 200, image: imagen });
let platform8 = new Platform({ x: 1900, y: canvas.height - 500, width: 200, image: imagen });
let platforms = [platform1, platform2, platform4, platform3, platform5, platform6, platform7, platform8]

let genericObjects = [
  new GenericObject({ x: 320, y: canvas.height - 200, width: 200, height: 100, image: hills }),
  new GenericObject({ x: 920, y: canvas.height - 300, width: 200, height: 100, image: hills }),
  new GenericObject({ x: 1500, y: canvas.height - 500, width: 200, height: 100, image: hills }),
  new GenericObject({ x: 0, y: 0, width: canvas.width, height: canvas.height, image: background })

]

const keys = {
  right: {
    pressed: false
  },
  left: {
    pressed: false
  }
}

let scrollOffset = 0

function init() {
  genericObjects = [
    new GenericObject({ x: 320, y: canvas.height - 200, width: 200, height: 100, image: hills }),
    new GenericObject({ x: 920, y: canvas.height - 300, width: 200, height: 100, image: hills }),
    new GenericObject({ x: 1500, y: canvas.height - 500, width: 200, height: 100, image: hills }),
    new GenericObject({ x: 0, y: 0, width: canvas.width, height: canvas.height, image: background })

  ]
  platform1 = new Platform({ x: 20, y: canvas.height - 100, width: 200, image: imagen });
  platform2 = new Platform({ x: 320, y: canvas.height - 300, width: 200, image: imagen });
  platform3 = new Platform({ x: 320, y: canvas.height - 200, width: 200, image: imagen });
  platform4 = new Platform({ x: 920, y: canvas.height - 400, width: 200, image: imagen });
  platform5 = new Platform({ x: 1220, y: canvas.height - 500, width: 200, image: imagen });
  platform6 = new Platform({ x: 1500, y: canvas.height - 200, width: 200, image: imagen });
  platform7 = new Platform({ x: 1700, y: canvas.height - 400, width: 200, image: imagen });
  platform8 = new Platform({ x: 1900, y: canvas.height - 500, width: 200, image: imagen });
  platforms = [platform1, platform2, platform4, platform3, platform5, platform6, platform7, platform8]
  scrollOffset = 0

  player = new Player();
  player.position.x = 100
  player.position.y = 100
}
function animate() {
  requestAnimationFrame(animate);

  // Limpiar pantalla
  c.fillStyle = 'rgba(255,255,255,1)'
  c.fillRect(0, 0, canvas.width, canvas.height);



  // Dibujar objetos
  genericObjects.forEach(genericObject => {
    genericObject.draw();
  })
  // Dibujar plataformas
  platforms.forEach(platform => {
    platform.draw();
  })
  // Dibujar jugador
  player.update();
  // Teclado
  if (keys.right.pressed && player.position.x < 400) {
    player.velocity.x = 5;
  } else if ((keys.left.pressed && player.position.x > 100) || keys.left.pressed && scrollOffset == 0 && player.position.x > 0) {
    player.velocity.x = -5;
  } else {
    player.velocity.x = 0
    if (keys.right.pressed) {
      scrollOffset += 5
      platforms.forEach(platform => {
        platform.position.x -= 5
      })
    } else if (keys.left.pressed) {
      scrollOffset -= 5
      platforms.forEach(platform => {
        platform.position.x += 5
      })
    }
  }

  // Colisiones plataformas - jugadors
  platforms.forEach(platform => {
    if (player.position.y + player.height <= platform.position.y &&
      player.position.y + player.height + player.velocity.y >= platform.position.y &&
      player.position.x + player.width >= platform.position.x &&
      player.position.x <= platform.position.x + platform.width
    ) {
      player.velocity.y = 0;
    }
  })


  // WIN
  if (scrollOffset > 2000) {
    console.log('YOU WIN!!!')
  }
  // LOSE
  if (player.position.y + player.height >= canvas.height) {
    console.log('YOU LOSE')
    init()
  }
}
init()
animate();

// ----------------------
// Teclado
addEventListener('keydown', (event) => {
  /**
   * event.keyCode
   * W - 87
   * A - 65
   * S - 83
   * D - 68
   * space - 32
   */
  switch (event.keyCode) {
    case 87: // UP
      player.velocity.y -= 20 // gravity
      break
    case 65: // LEFT
      keys.left.pressed = true
      player.currentSprite = player.sprites.run.left
      player.currentCropWidth = player.sprites.run.cropWidth
      player.width = player.sprites.run.width
      break
    case 83: // DOWN
      break
    case 68: // RIGHT
      keys.right.pressed = true
      player.currentSprite = player.sprites.run.right
      player.currentCropWidth = player.sprites.run.cropWidth
      player.width = player.sprites.run.width
      break
    case 32: // SPACE BAR
      break
  }
})
addEventListener('keyup', (event) => {
  /**
   * event.keyCode
   * W - 87
   * A - 65
   * S - 83
   * D - 68
   * space - 32
   */
  switch (event.keyCode) {
    case 87: // UP
      break
    case 65: // LEFT
      keys.left.pressed = false
      player.currentSprite = player.sprites.stand.left
      player.currentCropWidth = player.sprites.stand.cropWidth
      player.width = player.sprites.stand.width
      break
    case 83: // DOWN
      break
    case 68: // RIGHT
      keys.right.pressed = false
      player.currentSprite = player.sprites.stand.right
      player.currentCropWidth = player.sprites.stand.cropWidth
      player.width = player.sprites.stand.width
      break
    case 32: // SPACE BAR
      break
  }
})